.TodoListItem {
    padding: 1rem;
    display: flex;
    align-items: center;
    &:nth-child(even){
        background: #f8f9fa;
    }
    .checkbox{
        cursor: pointer;
        flex: 1;
        display: flex;
        align-items: center;    //세로 중앙정렬
        svg{
            font-size: 1.5rem;
        }
        .text {
            margin-left: 0.5rem;
            flex: 1;    //차지할 수 있는 영역 모두
        }
        //체크되었을때 보여줄 스타일
        &.checked {
            svg {
                color: #22b8cf;
            }
            .text{
                color: #adb5bd;
                text-decoration: line-through;
            }
        }
    }
    .remove {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        color: #ff6b6b;
        cursor: pointer;
        &:hover {
            color: #ff8787;
        }
    }

    //엘리먼트 사이사이에 테두리 넣어 줌
    & + & {
        border-top: 1px solid #dee2e6;
    }
}